@import '../../Style/variables.scss';
@import '../../Style/animations.scss';


$customerSpaceBoxSize: 260px;
$customerSpaceBoxHalfSize: calc(260px/2);
$customerSpaceBoxPosition: calc(50vw - $customerSpaceBoxHalfSize);


.customerSpace__box {
    display: block;
    width: 100%;
    max-width: $customerSpaceBoxSize;
    // margin-right: 15px;
    // vertical-align: top;
    font-family: 'Open Sans', sans-serif;
    margin: auto;
    padding: 10px 32px 10px 10px;
    background-color: white;
    border: 4px ridge $primary-color;
    z-index: 1;

    &::before {
        content: "";
        position: fixed;
        top: -1000%;
        left: -1000%;
        right: -1000%;
        bottom: -1000%;
        background-color: rgba($color: #000000, $alpha: 0.5);
        z-index: -10;
    }

    &::after {
        content: "";
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: white;
        z-index: -1;
    }


    &.navbar {
        position: fixed;
        top: 130px;
        right: 30px;
        animation: showForm 200ms ease-out 10ms forwards;
    }

    &.home {
        position: fixed;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        animation: showForm 200ms ease-out 10ms forwards;
        z-index: 2;
    }

    &-Off {
        display: none !important;
    }

    .customerSpace__title {
        margin: 0 0 10px 0;
        text-align: center;
        font-size: 1em;
    }

    &__closingCross {
        font-size: 1.3em;
        position: absolute;
        top: -12px;
        right: -12px;
        color: $primary-color;
        background-color: black;
        border-radius: 25%;
        padding: 0 1.5px;
    }
}

.client__form {
    // display: flex;
    // justify-content: center;
}

.client__formInput {
    width: 100%;
    background: #D8D8D8;
    border: 1px solid #ccc;
    padding: 5px 10px;
    color: #555;
    margin-bottom: 5px;
}

.client__formButton {
    // display: block;
    // align-self: center;
    width: 100%;
    border: 0;
    padding: 5px;
    color: #ffffff;
    background-color: #26ae90;
    font-size: 13px;
    margin-top: 5px;
    text-decoration: none;
    text-align: center;
    border-radius: 2px;
    cursor: pointer;
    font-family: 'Open Sans', sans-serif;
    text-transform: uppercase;

}